import store from "../../store"

// 
const BASE_URL = '/'
//           
const DEV_URL = '/admin'
// 
const UPLOAD_URL = '/upload'

/**
 * tab
 * @param {*} path 
 */
export function closeTab(path = '') {
    if (!path) {
        path = store.state.active
    }
    store.commit('close_tab', path)
}


export {
    BASE_URL,
    DEV_URL,
    UPLOAD_URL
}