import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../pages/login'

import index from '../pages/console/index'
import user from '../pages/user/index'
import editUser from '../pages/user/edit'

import staff from '../pages/staff/index'
import add_staff from '../pages/staff/add'
import edit_staff from '../pages/staff/edit'

import auth_menu from '../pages/auth_menu/index'
import add_auth_menu from '../pages/auth_menu/add'
import edit_auth_menu from '../pages/auth_menu/edit'

import auth_role from '../pages/auth_role/index'
import add_auth_role from '../pages/auth_role/add'
import edit_auth_role from '../pages/auth_role/edit'


import business from '../pages/business/index'
import addBusiness from '../pages/business/add'
import editBusiness from '../pages/business/edit'
import Product from '../pages/product/index'
import addProduct from '../pages/product/add'
import editProduct from '../pages/product/edit'
import category from '../pages/category/index'
import addCategory from '../pages/category/add'
import editCategory from '../pages/category/edit'
import order from '../pages/order/index'
import Admin from '../pages/admin/index'
import addAdmin from '../pages/admin/add'
import editAdmin from '../pages/admin/edit'
import Rule from '../pages/rule/index'
import addRule from '../pages/rule/add'
import editRule from '../pages/rule/edit'
import code from '../pages/exchange_code/index'
Vue.use(VueRouter)

const router = new VueRouter({
    routes: [{
            path: '/login',
            name: '',
            component: login
        },
        {
            path: '/',
            name: '',
            component: index,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/staff',
            name: '',
            component: staff,
            meta: {
                keepAlive: true
            }
        }, {
            path: '/add-staff',
            name: '',
            component: add_staff,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-staff',
            name: '',
            component: edit_staff,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/auth-menu',
            name: '',
            component: auth_menu,
            meta: {
                keepAlive: true
            }
        }, {
            path: '/add-authmenu',
            name: '',
            component: add_auth_menu,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-auth-menu',
            name: '',
            component: edit_auth_menu,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/auth-role',
            name: '',
            component: auth_role,
            meta: {
                keepAlive: true
            }
        }, {
            path: '/add-auth-role',
            name: '',
            component: add_auth_role,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-auth-role',
            name: '',
            component: edit_auth_role,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/user',
            name: '',
            component: user,
            meta: {
                keepAlive: true
            }
        }, {
            path: '/edit-user',
            name: '',
            component: editUser,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/add-category',
            name: '',
            component: addCategory,
            meta: {
                keepAlive: true
            }
        }, {
            path: '/edit-category',
            name: '',
            component: editCategory,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/product',
            name: '',
            component: Product,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add-product',
            name: '',
            component: addProduct,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-product',
            name: '',
            component: editProduct,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/category',
            name: '',
            component: category,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add-category',
            name: '',
            component: addCategory,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-category',
            name: '',
            component: editCategory,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/business',
            name: '',
            component: business,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add-business',
            name: '',
            component: addBusiness,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-business',
            name: '',
            component: editBusiness,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/order',
            name: '',
            component: order,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/admin',
            name: '',
            component: Admin,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add-admin',
            name: '',
            component: addAdmin,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-admin',
            name: '',
            component: editAdmin,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/rule',
            name: '',
            component: Rule,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add-rule',
            name: '',
            component: addRule,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/edit-rule',
            name: '',
            component: editRule,
            meta: {
                keepAlive: false
            }
        }, {
            path: '/code',
            name: '',
            component: code,
            meta: {
                keepAlive: false
            }
        }
    ]
})
router.beforeEach(function (to, from, next) {
    let token = localStorage.getItem('token')
    //
    if (!token) {
        if (to.path !== '/login') {
            return next({
                path: '/login',
                name: ''
            })
        }
        next()
    }
    // 
    if (to.path === '/login') {
        if (token) {
            return next({
                path: '/',
                name: ''
            })
        } else {
            return next({
                path: '/login',
                name: '',
            })
        }
    }
    next()
})
export default router