<template>
  <div class="main">
    <div class="router">
      <div class="card">
        <div class="card-header">
          <el-breadcrumb>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>

          <el-button
            type="primary"
            icon="el-icon-refresh"
            circle
            @click="refresh"
          ></el-button>
        </div>
        <!---->
        <div class="search" style="display:flex;">
          <el-input
            placeholder=""
            v-model="title"
            class="input-with-select"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"
              ></el-button
            >
          </el-input>
        </div>
        <!---->
        <div class="create">
          <el-button type="primary" icon="el-icon-edit" @click="create"
            ></el-button
          >
          <span>: {{ total }} </span>
        </div>

        <!---->
        <div class="card-body text">
          <el-table :data="data" style="width: 100%">
            <el-table-column prop="id" label="ID" width="80"> </el-table-column>
            <el-table-column prop="pid_title" label="" width="120">
            </el-table-column>
            <el-table-column prop="title" label=""> </el-table-column>
            <el-table-column prop="image" label="">
              <template slot-scope="score">
                <el-avatar :src="score.row.image"></el-avatar>
              </template>
            </el-table-column>
            <el-table-column prop="remark" label=""> </el-table-column>
            <el-table-column prop="sort" width="200" label="">
              <template slot-scope="scope">
                <el-input
                  title=""
                  placeholder=""
                  v-model="scope.row.sort"
                  @blur="changeSort(scope.row.id, scope.row.sort)"
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="primary"
                  icon="el-icon-top"
                  v-if="!scope.row.status"
                  title=""
                  @click="change(scope.row.id, scope.$index, 1)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="info"
                  title=""
                  icon="el-icon-bottom"
                  v-else
                  @click="change(scope.row.id, scope.$index, 0)"
                >
                </el-button>
                <el-button
                  size="mini"
                  type="success"
                  icon="el-icon-edit"
                  @click="_edit(scope.row.id)"
                >
                </el-button>
                <el-button
                  @click="del(scope.row.id, scope.$index)"
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                >
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <!---->
      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="prev, pager, next"
          :page-size="paegsize"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { request } from '../../untils/js/request'
import store from '../../store'

export default {
  name: 'index',
  data() {
    return {
      data: [],
      title: '',
      page: 1,
      paegsize: 20,
      total: 0,
    }
  },
  created() {
    this._request()
  },
  methods: {
    async _request() {
      let res = await request(`/category?page=${this.page}&title=${this.title}`)
      if (res.error_code === 10000) {
        this.data = res.data.data
        this.total = res.data.total
        this.page = res.data.current_page
        this.paegsize = res.data.per_page
      }
    },
    create() {
      store.commit('add_tabs', { path: 'add-category', name: '' })
    },
    async _change(id, form, key) {
      let res = await request(`/category/${id}`, form, 'PUT')
      if (res.error_code === 10000) {
        let value = Object.keys(form)[0]
        if (value === 'status') {
          this.data[key][value] = Object.values(form)[0]
        }
        this.$message.success('')
      } else {
        this.$message.error('')
      }
    },
    async _detele(id, key) {
      let res = await request(`/category/${id}`, '', 'DELETE')
      if (res.error_code === 10000) {
        this.$message('')
        this.data.splice(key, 1)
      }
    },
    change(id, key, status) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._change(id, { status: status }, key)
      })
    },
    changeSort(id, sort) {
      this._change(id, { sort: sort })
    },
    del(id, key) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        this._detele(id, key)
      })
    },
    _edit(id) {
      store.commit('setid', id)
      store.commit('add_tabs', { path: 'edit-category', name: '' })
    },
    handleCurrentChange(e) {
      this.page = e
      this._request()
    },
    search() {
      if (this.title) {
        this._request()
      } else {
        this.$message.error('')
      }
    },
    refresh() {
      this.page = 1
      this.title = ''
      this._request()
    },
  },
}
</script>

<style scoped></style>
